*{
    margin: 0;
    padding: 0;
}
.header_img{
    display: inline-block;
    
}
.header_img img{
    width: 100px;
    object-fit: contain;
    margin: 0px 20px;
    
    
}
.header_main{
    display: flex;
    justify-content: center;
    width: 100%;
    
   
}
.header_main h3{
    display: flex;
    text-align: center;
    align-items: center;
    margin: 20px;
    font-size: 30px;
    
}
.nav{
    width: 100%;
    text-align: center; 
    z-index: 9999;
    background-color: black;
    box-shadow: 1px 1px 1.5px 1.5px rgb(21, 21, 21);
}
.nav li{ 
    
    display: inline-block;
    margin:15px 20px;
}
.nav li a{
    text-decoration: none;
    color: white;
    margin: 20px;
}
.main_menu li:hover{
    background-color: rgb(39, 39, 39);
}
